.art {
  flex: 1;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.artContainer {
  width: 100%;
  height: 100%;
  display: flex;
  align-items: center;
  overflow: hidden;
  position: relative;
  max-height: 100%;
}

.artPadding {
  width: 100%;
  height: 100%;
  padding-left: 24px;
  display: flex;
}

@media (max-aspect-ratio: 7/8) {
  .artPadding {
    padding-left: 0;
  }
}

.artPadding,
.copyContainer {
  box-sizing: border-box;
}

.static {
  filter: blur(5px);
  left: 0;
  mix-blend-mode: hard-light;
  opacity: 1;
  overflow: hidden;
  position: absolute;
  top: 0;
  transition: opacity 200ms ease-in;
}

.static.show {
  opacity: 1;
}

.sidebars {
  position: absolute;
  top: 0;
  left: 0;
  pointer-events: none;
  z-index: 1;
}

.container {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
  background-position: center center;
  background-size: cover;
  color: #444444;
  padding-left: 24px;
  padding-right: 24px;
  box-sizing: border-box;
}

.content {
  box-sizing: border-box;
  max-width: 1600px;
  margin: auto;
  height: 100%;
  display: flex;
  flex-direction: row;
}

.copy {
  flex: 1;
  width: 100%;
  padding-right: 12px;
  display: flex;
  flex-direction: column;
}

.copyContainer {
  flex: 1;
  width: 100%;
  padding-right: 12px;
  display: flex;
  align-items: top;
  padding-top: 48px;
  padding-bottom: 48px;
}

.video {
  width: '100%';
  height: '100%';
  object-fit: 'cover';
  display: 'block';
}

.link {
  text-decoration: underline;
}
